<template>
	<div id="printer" class="warp">
		<nav class="nvs">
			<el-breadcrumb separator=">">
				<el-breadcrumb-item :to="{ path: '/shop' }">店铺管理</el-breadcrumb-item>
				<el-breadcrumb-item :to="{ path:'/shop/printer'}">打印机管理</el-breadcrumb-item>
				<el-breadcrumb-item>编辑打印机</el-breadcrumb-item>
			</el-breadcrumb>
		</nav>
		<div class="printer-info">
			<div class="title">
				编辑打印机
			</div>
			<div class="info-warp">
				
				<el-form ref="form" label-width="120px">
					<el-form-item label="打印机ID">
						<span>{{printerID}}</span>
					</el-form-item>
					<el-form-item label="名称">
						<el-input v-model='form.printer_name'></el-input>
					</el-form-item>
					<el-form-item label="规格">
						 <el-radio v-model="form.printer_spec" label=58>58mm</el-radio>
 						 <el-radio v-model="form.printer_spec" label=80>80mm</el-radio>
					</el-form-item>
					<el-form-item label='支持单据类型'>
						<el-checkbox-group v-model="type">
							<el-checkbox :label='documentsType.MENUSAFTERKITCHEN' name='type'>{{documentsType.toString(documentsType.MENUSAFTERKITCHEN)}}</el-checkbox>
							<el-checkbox :label='documentsType.INVALIDSINGLE' name='type'>{{documentsType.toString(documentsType.INVALIDSINGLE)}}</el-checkbox>
							<el-checkbox :label='documentsType.MENUCONSUMER' name='type'>{{documentsType.toString(documentsType.MENUCONSUMER)}}</el-checkbox>
							<el-checkbox :label='documentsType.STATEMENT' name='type'>{{documentsType.toString(documentsType.STATEMENT)}}</el-checkbox>
							<el-checkbox :label='documentsType.PRESTATEMENT' name='type'>{{documentsType.toString(documentsType.PRESTATEMENT)}}</el-checkbox>
						</el-checkbox-group>
					</el-form-item>	
					<el-form-item class='goods' label='后厨单打印分类'>
						<div class="classion" v-for='(item,index) in classionf'>
							<el-select v-model='item.value1' placeholder="请选择" >
								<el-option v-for='item in options1' :key="item.idx" :label="item.label" :value="item.idx">
								</el-option>
							</el-select>
							<el-select v-model='item.value2' placeholder="全部" >
								<el-option v-for='item in options2' :key='item.idx':label="item.label" :value="item.idx">
								</el-option>
							</el-select>
							<el-select v-model='item.value3' placeholder="全部">
								<el-option v-for='item in options3' :key='item.idx':label="item.label" :value="item.idx" >
								</el-option>
							</el-select>
							<span class="adds" @click="addcls" v-if='item.showAdd'>添加所属分类</span>
							<span class="del" @click="delcls(index)" v-if='item.showDel'></span>
						</div>
					</el-form-item>
					<el-form-item label="型号">
						<el-input v-model='form.printer_model'></el-input>
					</el-form-item><el-form-item label="备注">
						<el-input type='textarea' v-model='form.remark' placeholder='备注文字总数不得超过120字'></el-input>
					</el-form-item>
				</el-form>
			</div>
		</div>
		<div class="hei_seting">
			<div class="title">
				高级设置
			</div>
			<div class="seting-positon">
				<span class="text">打印位置调整 </span><span class="lp"> Left </span> <input type="text" /> mm <span class="tp">Top</span> <input type="text" /> mm
			</div>
			<div class="btn">
				<button type="button" class="blue" @click="onSubmit">保存</button>
				<button type="button" class="default">取消</button>
			</div>
		</div>
	</div>
</template>

<script>
	import { documentsType } from '@/config/cfg';
	import { getCategory,getPrinter } from '../../api';
	export default{
		data(){
			return{
				form:{
					printer_name:'',
					printer_spec:'',
					printer_model:'',
					remark:''
				},
				classionf:[
					{
						value1:'',
						value2:'',
						value3:'',
						showAdd:true,
						showDel:false
					}
					
				],
				type:[],
				list:[],
				options1:[],
				options2:[],
				options3:[],
				category:[],
				printers:[],
				printerID:null
			}
		},
		mounted(){
			this.getData();
//			this.setData();
		},
		computed:{
			documentsType(){
				return documentsType;
			}
		},
		methods:{
			onSubmit(){
				if(this.check(this.form.remark)){
					return ;
				}
			},
			addcls(){
				let data = {
						value1:'',
						value2:'',
						value3:'',
						showAdd:false,
						showDel:true
				};
				this.classionf.push(data);
			},
			delcls(index){
				this.classionf.splice(index,1);
			},
			getData(){
				let _this = this;
				getCategory({
					list:1
				},function(resp){
					_this.list = resp.data.list;
				});
				getPrinter({
					
				},function(resp){
					let prtId = _this.$route.query.id;
					_this.printers = resp.data.printer || [];
					_this.printers.forEach(item=>{
						if(item.printer_id==prtId){
							_this.form.printer_name = item.printer_name;
							_this.form.printer_spec = String(item.printer_spec);
							_this.type = item.documents_type;
							_this.form.printer_model = item.printer_model;
							_this.form.remark = item.remark;
							_this.printerID = item.printer_id;
						}
					});
				})
			},
//			getFirstlist(val){
////				this.options2 = this.getOptions(this.list[val].list);
////   			this.category[0] = this.options1[val].id;
//			},
//			getSeclist(val){
////				this.category[1] = this.options2[val].id;
////   			this.options3 = this.getOptions(this.list[this.value1].list[val].list);	
//			},
//			getThrlist(val){
////				this.category[2] = this.options3[val].id;
//			},
//			setData(){
////   			 this.options1 = this.getOptions(this.list);
//  		},
//			getOptions(arr){
//			      return arr.map((item,idx)=>{
//			        return {
//			         idx,
//			         label:item.category_name,
//			         id:item.category_id
//			        }
//			      })
//			},
			 getLen(str) {
			      if (str == null) return 0;
			      if (typeof str !== "string") {
			        str += "";
			      }
			      return str.replace(/[^\x00-\xff]/g, "01").length;
			    },

		    //remark check
		    check() {
			      if (this.getLen(this.form.remark) > 240) {
			        this.$notify({
			          title: "警告",
			          message: "备注超过120字",
			          type: "warning"
			        });
			        return true;
			      }
			 }
		}
	}
</script>

<style lang="scss" scoped>
	@import 'src/styles/mixin.scss';
	.warp{
		background:#fff;
		.nvs {
		background: #EFF3FA;
		height: 26px;
		}
		button{
			cursor: pointer;
		}
		.printer-info{
			.title {
				@include fc($sizes, $grey);
				height: 40px;
				line-height: 40px;
				background: #F6F8FC;
				text-indent: 14px;
			}
			.info-warp{
				padding-top:40px;
				margin-left: 128px;
			}
			.adds{
				position: relative;
				cursor: pointer;
				display: inline-block;
				@include wh(150px,36px);
				border: 1px solid #4877E7;
				@include fc(14px,#4877E7);
				padding-left: 45px;
				&:before{
					position: absolute;
					content: '';
					left:24px;
					top:10px;
					background: url(../img/add1.png)no-repeat;
					@include wh(16px,16px);
				}
			}
			.del{
				position: relative;
				cursor:pointer; 
				&:before{
					position: absolute;
					content: '';
					left: 0;
					top: 0;
					background: url(../img/del.png)no-repeat;
					@include wh(24px,24px);
				}
			}
		}
		.hei_seting{
			height:204px;
			padding:0 0 50px 0;
			.title {
				@include fc($sizes, $grey);
				height: 40px;
				line-height: 40px;
				background: #F6F8FC;
				text-indent: 14px;
			}
			.seting-positon{
				margin:40px 0  40px 142px;
				.text{
					@include fc(14px,#000);
					opacity:0.57;
				}
				.lp{
						margin-left: 14px;
						color:#333;
					}
					.tp{
						margin-left: 56px;
						color:#333;
					}
				input{
					@include wh(50px,34px);
					border:1px solid #d9d9d9;
				}
			}
			.btn{
				text-align: center;
				padding-bottom: 40px;
				background: #fff;
				.blue {
				@include fc(16px, #ffffff);
				background: #4877E7;
				@include wh(160px, 40px);
				line-height: 40px;
				border-radius: 6px;
			}
			.default {
				@include fc(14px, #4877E7);
				background: #ffffff;
				@include wh(160px, 40px);
				line-height: 40px;
				border-radius: 6px;
				border: 1px solid #4877E7;
				margin-left: 50px;
			}
			}
		}
	}
	
</style>
<style lang="scss">
	@import 'src/styles/mixin.scss';
	#printer {
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner a,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner a:hover,
		.el-breadcrumb__item:last-child .el-breadcrumb__item__inner:hover {
			color: #4877E7;
		}
		.el-input__inner {
			border: 1px solid #d9d9d9;
			border-radius: 0;
			@include wh(280px, 34px);
		}
		.goods{
			max-width: 860px;
		}
		.goods input{
			border: 1px solid #d9d9d9;
			border-radius: 0;
			@include wh(180px, 34px);
		}
		.el-select{
			margin-right:8px;
			margin-bottom:8px;
		}
		.el-textarea__inner {
			@include wh(400px, 180px);
			resize: none;
			border-radius: 0;
			border: 1px solid #D9D9D9;
		}
		.el-select .el-input .el-input__icon{
			background: #f2f2f2;
		}
		.el-form-item__label{
			color: #000;
			opacity: 0.57;
		}
	}
</style>


